html,


body {
  background-color: #ffffff;
  /*首先宣告我們要使用flex佈局*/
  display: -webkit-flex;
  display: flex;

  /*讓我們的flex佈局，由上到下排列*/
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow-x: hidden;
  /*高度設定為 可大於但不可小於 整個瀏覽器可視範圍*/
}


.container {
  /*設置flex-grow: 1，讓它能夠彈性伸展*/
  -webkit-flex-grow: 1;
          flex-grow: 1
    /*以下兩種方式也可以達到一樣效果，flex是三種屬性的縮寫*/
    /*flex: 1等於 flex-grow: 1; flex-shrink: 1; flex-basis: 0%;*/
    /*flex: auto等於 flex-grow: 1; flex-shrink: 1; flex-basis: auto;*/

    /*flex: 1; */
    /*flex: auto;*/
}


.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  top: 0;
  width: 80%;
  background-color: #6200ff;
}

.center-body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}

.form-signin .form-control:focus {
  z-index: 2;
}

.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

#grade {
  border-color: '#69b6e8';
  border-style: 'rounded';
  border-radius: 8px;
}

#rightbtn {

  position: fixed;
  /*固定在網頁上不隨卷軸移動，若要隨卷軸移動用absolute*/

  top: 50%;
  /*設置垂直位置*/

  right: 1%;
  /*設置水平位置，依所放的內容多寡需要自行手動調整*/

  background: #ffffff;
  /*背景顏色*/

  padding: 10px 20px;

  border-radius: 10px;
  /*圓角*/

  -moz-border-radius: 10px;

  -webkit-border-radius: 10px;

  color: blue;
}

#leftbtn {

  position: fixed;
  /*固定在網頁上不隨卷軸移動，若要隨卷軸移動用absolute*/

  top: 50%;
  /*設置垂直位置*/

  left: 1%;
  /*設置水平位置，依所放的內容多寡需要自行手動調整*/

  background: #ffffff;
  /*背景顏色*/

  padding: 10px 20px;

  border-radius: 10px;
  /*圓角*/

  -moz-border-radius: 10px;

  -webkit-border-radius: 10px;

  color: blue;
}

#exbtn:hover {
  /*當滑鼠移至此區塊時，伸縮區塊*/

  top: 51%;

}

#exbtn #title {

  padding-right: 5px;
  /*讓標題與連結中間有空隙*/
}

#input {
  border: none;
  border-bottom: 2px solid blue;
  text-transform: uppercase;
}

#input[value=''] {
  border: none;
  border-bottom: 2px solid red;
  text-transform: uppercase;
}

